@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;700&display=swap);
html {
    scrollbar-width: none;
}

body {
    min-width: 1020px;
    overflow: scroll;
    overflow-x: hidden;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
    display: none;
}

@font-face {
    font-family: 'Avenir Next Cyr';
    src: url(/static/media/Metropolis-SemiBold.5e856013.otf);
}

.top-bar {
    display: flex;
    height: 65px;
    flex-direction: row;
    align-items: center;
    padding: 0 23%;
    justify-content: space-between;
}

.top-bar-item {
    display: flex;
    height: 65px;
    flex-direction: row;
    align-items: center;
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 16px;
    color: black;
    font-weight: normal;
}

.top-bar-item div {
    margin: 0 30px;
}

.main-body {
    position: relative;
    display: flex;
    flex-direction: row;
    z-index: 1;
}

.main-body-item {
    display: flex;
    width: 50%;
    justify-content: center;
    padding-top: 50px;
    align-items: baseline;
}

.floating-text {
    font-family: 'IBM Plex Sans', sans-serif;
    position: absolute;
    z-index: 10;
    left: 23%;
    top: 100px;
}

.floating-text-title {
    font-size: 48px;
    color: #057EFF;
    font-weight: bold;
    margin: 30px 0;
}

.floating-text-small {
    font-size: 18px;
    color: #525252;
    margin-bottom: 40px;
}

.ButtonBase {
    position: relative;
    font-family: 'IBM Plex Sans', sans-serif;
    text-align: center;
    display: table;
}

.ButtonBase:hover {
    cursor: pointer;
}

